
.el-footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  background-color: #545c64;
  border-color: rgba(238, 241, 146, 0.3);
  border-top-width: 1px;
  border-top-style: solid;
  color: #fff;
  text-align: center;
  line-height: 60px;
  z-index: 10;
}
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  background-color: #f9f9f9;
  width: 100%;
}
.container .header {
    height: 60px;
    line-height: 60px;
    background: #545c64;
    color: #fff;
}
.container .header .userinfo {
      text-align: right;
      padding-right: 35px;
      float: right;
}
.container .header .userinfo .userinfo-inner {
        cursor: pointer;
        color: #fff;
}
.container .header .userinfo .userinfo-inner img {
          width: 40px;
          height: 40px;
          border-radius: 20px;
          margin: 10px 0px 10px 10px;
          float: right;
}
.container .header .lang {
      text-align: right;
      padding-right: 40px;
      float: right;
}
.container .header .logo {
      font-family: -webkit-pictograph;
      height: 60px;
      font-size: 22px;
      padding-left: 20px;
      padding-right: 20px;
      border-color: rgba(238, 241, 146, 0.3);
      border-right-width: 1px;
      border-bottom-width: 1px;
      border-right-style: solid;
      border-bottom-style: solid;
}
.container .header .logo img {
        width: 40px;
        float: left;
        margin: 10px 10px 10px 18px;
}
.container .header .logo .txt {
        color: #fff;
}
.container .header .logo-width {
      width: 230px;
}
.container .header .logo-collapse-width {
      width: 65px;
}
.container .header .tools {
      padding: 0px 23px;
      width: 14px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
}
.container .main {
    display: flex;
    position: absolute;
    top: 60px;
    bottom: 0px;
    overflow: hidden;
}
.container .main aside {
      flex: 0 0 230px;
      width: 230px;
}
.container .main aside .el-menu {
        height: 100%;
}
.container .main aside .collapsed {
        width: 60px;
}
.container .main aside .collapsed .item {
          position: relative;
}
.container .main aside .collapsed .submenu {
          position: absolute;
          top: 0px;
          left: 60px;
          z-index: 99999;
          height: auto;
          display: none;
}
.container .main .menu-collapsed {
      flex: 0 0 60px;
      width: 60px;
}
.container .main .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
}
.container .main .content-container {
      flex: 1;
      overflow-y: scroll;
      padding: 20px;
      margin-bottom: 60px;
}
.container .main .content-container .breadcrumb-container .title {
        width: 200px;
        float: left;
        color: #475669;
}
.container .main .content-container .breadcrumb-container .breadcrumb-inner {
        float: right;
}
.container .main .content-container .content-wrapper {
        background-color: #fff;
        box-sizing: border-box;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader {
  text-align: center;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}



